@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Roboto:wght@300;400;700&display=swap);
*, *:after, *:before{
  margin: 0;
  list-style:none;
  text-decoration: none;
  box-sizing: border-box;
}
html{
  font-size: 10px;
  box-sizing: inherit;
  background-color: #36243b;
  width: 100vw;
  overflow-x: hidden;
  min-height: 150vh;
  scroll-behavior: smooth;
}
button, button:active, button:focus, input, input:active, input:focus{
  outline: none!important;
  border: none!important;
  cursor: pointer;
}

/************************************************/
h1,h2,button{
  font-family: 'Bebas Neue', sans-serif;
  color: white;
}
p,input,a{
  font-family: 'Roboto', sans-serif;
  color: white;
}
header{
    margin: 0 -30px;
    border-radius: 0 0 10px 10px;
    box-shadow: 0px 0px 4px black;
    display: flex;
    justify-content: space-between;
    background-color: #12161c;
    padding: 0 2rem;
    height: 6vh;
}
.header-nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 30%;
}
h1{
    font-size: 2.5rem;
    letter-spacing: 4px;
}
.icon{
    color: white;
    font-size: 1.8rem;
}
a{
    font-size: 1.4rem;
    text-transform: uppercase;
    font-family: 'Bebas Neue';
    letter-spacing: 2px;
}
.header-search{
    width: 40%;
    display: flex;
    justify-content: center;
    padding-left: 10rem;
    align-items: center;
}
.search-bar{
    padding: 0 .5rem;
    width: 40%;
    border-radius: 6px;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}
input{
    border-radius: 6px;
    padding: .5rem .5rem;
    width: 95%;
    color: black;
    font-family: 'Roboto', sans-serif;
    letter-spacing: 1px;
}
.search-bar:nth-child(1){
    font-size: 1.3rem;
}
.header-user{
    padding: 0 5rem 0 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 30%;
}
.header-user > *{
    margin-right:3rem;
    cursor: pointer;
}
.header-user img{
    width: 30px;
    height: 30px;
    background-color: black;
    border-radius: 50%;
}

a:hover, .icon:hover{
    color:#4a487c
}
a,.icon{
    cursor: pointer;
}
.hero{
    height: 94vh;
    background-color: #12161c;
    width: 100%;
}
.hero-image{
    height: 85%;
    background-image: url(/static/media/hero.f069f4c1.jpg);
    background-position: center center;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
}
.hero-image p{
    font-size: 10rem;
    font-family: 'Bebas Neue';
    text-shadow: 0px 0px 3px black;
}
.hero-sponsors{
    height: 15%;
    max-height: 15%;
    overflow: hidden;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.hero-sponsors > *{
    width:5%;
    cursor: pointer;
}
.products{
    padding: 10rem;
    min-height: 100vh;
}
.products-container{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 90px 30px;
    margin-top: 3rem;
}
.product-item{
    border-radius: 7px;
    overflow: hidden;
    background-color: #12161c;
    cursor: pointer;
    height: 28vh;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    box-shadow: 0px 0px 5px rgb(179, 178, 178);
    transition: box-shadow 500ms ease;
}
.product-item:hover {
    box-shadow: 0px 0px 10px black;
}
.product-item:hover .product-img-container > p{
    bottom: 0px;
}
.product-item:hover .product-img-container > img{
    -webkit-filter: grayscale(0);
            filter: grayscale(0);
}
.product-img-container{
    position: relative;
    overflow: hidden;
    border-radius: 7px 7px 0 0;
    height: 80%;
}
.product-img-container p{
    padding: 1rem;
    position: absolute;
    left: 0;
    right: 0;
    bottom: -80px;
    background-color: rgba(0,0,0,0.4);
    font-size: 1.5rem;
    transition: bottom 200ms ease;
}
.product-img-container > img{
    max-width: 100%;
    width: 100%;
    height: 100%;
    -webkit-filter: grayscale(1);
            filter: grayscale(1);
    transition: 500ms ease;
    will-change: filter;
}
.product-price{
    font-size: 1.5rem;
    font-weight: bolder;
    color: white;
    padding: 2rem 2rem 0 0;
    color: rgb(243, 118, 96)
}
.monetizing{
    background-color: rgb(235, 235, 235);
    padding: 5rem;
}
.monetizing-flex{
    margin: 3rem auto;
    width: 70%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.monetizing-left{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-content: flex-start;
}
.monetizing-left p{
    color: #12161c;
    font-size: 2rem;
}
footer{
    background-color: #12161c;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-around;
    padding: 7rem 5rem;
}
.footer-mid{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    padding: 0 5rem;
}
.footer-mid > *{
    margin: 0 5rem;
}
.footer-mid nav{
    margin-top: 5rem;
}
.footer-mid nav ul{
    padding-left: 10px;
}
.footer-mid nav ul li{
    text-align: left;
    margin-top: 20px;
}
.footer-mid nav ul li a{
    font-family: 'Roboto', sans-serif;
    text-transform: none;
}
.footer-mid h2, .footer-right h2{
    color: white;
}
footer h1{
    font-size: 4rem;
}
.footer-right{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-start;

}
.footer-right input{
    margin-top: 5rem;
}
.footer-right button{
    margin: 1rem 0;
}

body{
  margin: 0 auto;
  min-height: 100vh;
  width: 80%;
  background-color: white;
  box-shadow: 0px 0px 10px black;

}
.btn-blue{
  background-color: #447edd;
  margin-left: 5rem;
  padding: .5rem 2rem;
  border-radius: 7px;
  font-size: 1.7rem;
}
.btn-blue:active{
  background-color: #3e70c2;
  transform: scale(0.98);
}
h2{
  text-align: center;
  font-size: 4rem;
  letter-spacing: 3px;
  color: #000;
}
